<template>
  <div class="result-view">
    <h2></h2>
    <ResultField label="" :value="fio"></ResultField>
    <ResultField label="" :value="data.phone"></ResultField>
    <ResultField label="  " checkbox v-if="data.noSMS"></ResultField>
    <ResultField label=" " :value="dateOfBirth"></ResultField>
    <ResultField label="" :value="sex"></ResultField>
  </div>
</template>

<script>
import ResultField from "@/components/form/views/ResultField.vue";
import {formatDate} from "@/utils.js";
export default {
  name: "PatientView",
  components: {ResultField},
  props: {
    data: {
      type: Object,
      required: true,
    }
  },
  computed: {
    fio() {
      return [this.data.lastName, this.data.firstName, this.data.patronymic].join(' ').trim();
    },
    sex() {
      switch (this.data.sex) {
        case 'male': {
          return '';
        }
        case 'female': {
          return '';
        }
        default: {
          return ' '
        }
      }
    },
    dateOfBirth() {
      return this.data.dateOfBirth ? formatDate(this.data.dateOfBirth) : ' ';
    }
  }
}
</script>

