<template>
  <div class="result-view">
    <h2></h2>
    <ResultField label=" " :value="documentType"></ResultField>
    <ResultField label=" " :value="data.issuedAt"></ResultField>
    <ResultField label=" " :value="issueDate"></ResultField>
    <ResultField label="  " :value="id"></ResultField>
  </div>
</template>

<script>
import ResultField from "@/components/form/views/ResultField.vue";
import {formatDate} from "@/utils.js";
export default {
  name: "DocumentView",
  components: {ResultField},
  props: {
    data: {
      type: Object,
      required: true,
    }
  },
  computed: {
    id() {
      return [this.data.series, this.data.number].join(' ').trim();
    },
    documentType() {
      switch (this.data.type) {
        case 'passport': {
          return '';
        }
        case 'birth-certificate': {
          return '';
        }
        case 'driver-license': {
          return '';
        }
        default: {
          return '';
        }
      }
    },
    issueDate() {
      return this.data.issueDate ? formatDate(this.data.issueDate) : ' ';
    }
  }
}
</script>

